.ant-row.ant-form-item {
	margin-bottom: 12px;
}
.ant-collapse-header {
	font-weight: bold;
}
.ant-form-item label {
	margin-bottom: 0px;
	font-weight: bold;
	font-size: 12px !important;
}
.ant-form-item-children .ant-btn-group-sm {
	margin-right: 12px;
}
form .ant-select-sm,
form .ant-btn-group-sm > .ant-btn,
body .ant-btn-sm,
body .ant-input-group-wrapper-sm span,
body .ant-input-group-wrapper-sm input {
	font-size: 12px;
}
.ant-btn-group-sm .ant-btn:first-child,
body .ant-input-group-wrapper .ant-input-group-addon {
	font-weight: 500;
}